header{
    height:100vh;
    padding-top: 7rem;
    overflow: hidden;
}
.header__container{
    text-align: center;
    height: 100%;
    position: relative;
}


/*CTA*/

.cta{
    margin-top: 2.rem;
    display: flex;
    gap: 1.2rem;
    justify-content: center ;
    }

 .header__socials{
    display:flex;
    flex-direction: column;
    align-items: center;
    left: 0;
    position: absolute;
    bottom: 3rem;
    gap:0.8rem;
 }

 .header__socials::after{
    content:"";
    width:1px;
    height: 2rem;
    background: var(--color-primary);
 }

 /*scroll down*/

 .scroll__down{
position: absolute;
right: -2.3rem;
bottom: 5rem;
transform: rotate(90deg);
font-weight: 300;
font-size: 1rem;
 }

 /*Partie responsive pour tablette */

@media screen and(max-width:1024px){
    header{
height: 68vh;
    }
}

/*Partie responsive pour smartphone */

@media screen and(max-width:600px){
    header{
height: 100vh;
    }

    .header__socials,
    .scrolls__down{
        display: none;
    }
}

    .me{
        background: linear-gradient(var(--color-primary),transparent);
        width: 22rem;
        height: 30rem;
        position: absolute;
        left: calc(50% - 11rem);
        margin-top: 4rem;
        border-radius: 12rem 12rem 0 0;
        overflow: hidden;
        padding: 5rem 1.5rem 1.5rem 1.5rem;
    }